import { Button, Form, Typography } from 'antd';
import {
  useScanQrPopup,
  useShowPopup,
} from '@vkruglikov/react-telegram-web-app';

const ScanQrPopup = () => {
  const [showQrPopup, closeQrPopup] = useScanQrPopup();
  const showPopup = useShowPopup();

  return (
    <>
      <Typography.Title level={3}> QR </Typography.Title>
      <Form
        labelCol={{ span: 6 }}
        name="ScanQrPopup"
        layout="horizontal"
        autoComplete="off"
      >
        <Form.Item>
          <Button
            block
            type="primary"
            htmlType="button"
            onClick={() =>
              showQrPopup(
                {
                  text: ' ',
                },
                text => {
                  closeQrPopup();
                  showPopup({
                    message: text,
                  });
                },
              )
            }
          >
             QR 
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default ScanQrPopup;