import { Button, Form, Typography, Select } from 'antd';
import { FC, useState } from 'react';
import {
  ImpactOccurredFunction,
  NotificationOccurredFunction,
  useHapticFeedback,
} from '@vkruglikov/react-telegram-web-app';

const HapticFeedbackApp: FC = () => {
  const [impactOccurred, notificationOccurred, selectionChanged] =
    useHapticFeedback();
  const [style, setStyle] =
    useState<Parameters<ImpactOccurredFunction>[0]>('light');
  const [type, setType] =
    useState<Parameters<NotificationOccurredFunction>[0]>('error');

  return (
    <>
      <Typography.Title level={3}>  </Typography.Title>
      <Form
        labelCol={{ span: 6 }}
        name="HapticFeedback"
        layout="horizontal"
        autoComplete="off"
      >
        <Form.Item label="">
          <Select value={style} onChange={value => setStyle(value)}>
            <Select.Option value="light"></Select.Option>
            <Select.Option value="medium"></Select.Option>
            <Select.Option value="heavy"></Select.Option>
            <Select.Option value="rigid"></Select.Option>
            <Select.Option value="soft"></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item>
          <Button block type="primary" onClick={() => impactOccurred(style)}>
            
          </Button>
        </Form.Item>
        <Form.Item label="">
          <Select value={type} onChange={value => setType(value)}>
            <Select.Option value="error"></Select.Option>
            <Select.Option value="success"></Select.Option>
            <Select.Option value="warning"></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item>
          <Button
            block
            type="primary"
            onClick={() => notificationOccurred(type)}
          >
            
          </Button>
        </Form.Item>
        <Form.Item>
          <Button block type="primary" onClick={() => selectionChanged()}>
             
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};
export default HapticFeedbackApp;