import { Button, Form, Input, Typography } from 'antd';
import { FC, useState } from 'react';
import {
  ShowPopupParams,
  useShowPopup,
} from '@vkruglikov/react-telegram-web-app';

const ShowPopup: FC = () => {
  const showPopup = useShowPopup();
  const [popupState, setPopupState] = useState<
    Pick<ShowPopupParams, 'title' | 'message'>
  >({
    title: '',
    message: '',
  });
  const onFinish = (values: any) => {
    setPopupState(values);
    showPopup({
      ...values,
      buttons: [
        {
          type: 'ok',
          text: '',
        },
        {
          type: 'close',
          text: '',
        },
        {
          type: 'destructive',
          text: '',
        },
      ],
    }).catch(e => {
      showPopup({
        title: 'error',
        message: e,
      });
    });
  };

  return (
    <>
      <Typography.Title level={3}> </Typography.Title>
      <Form
        labelCol={{ span: 6 }}
        name="ShowPopup"
        layout="horizontal"
        initialValues={popupState}
        onFinish={onFinish}
        autoComplete="off"
      >
        <Form.Item label="" name="title">
          <Input />
        </Form.Item>
        <Form.Item label="" name="message">
          <Input />
        </Form.Item>
        <Form.Item label="" name="buttons">
          <Typography.Text>
            {JSON.stringify([
              {
                type: 'ok',
                text: '',
              },
              {
                type: 'close',
                text: '',
              },
              {
                type: 'destructive',
                text: '',
              },
            ])}
          </Typography.Text>
        </Form.Item>
        <Form.Item>
          <Button block type="primary" htmlType="submit">
              
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};
export default ShowPopup;